import React from "react";

// reactstrap components
import {
    Card,
    CardHeader,
    CardBody,
    CardTitle,
    Table,
    Row,
    Col, CardText, CardFooter, Button, CardLink
} from "reactstrap";

import FirebaseComponent, {FirebaseComp} from "../components/Firebase/FirebaseComponent"
import {withFirebase} from '../components/Firebase';

class BlogDetailsComponent extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            about: {
                name: "Ngima Sherpa",
                description: "loading...",
                designation: "loading...",
            },
            work: [],
            experience: [],
            skills: [],
            clients: [],
            blog: {},
            blogs: [],
            loading: false
        };
    }

    componentDidMount() {
        //array
        this.props.firebase.blogs().on('value', snapshot => {
            const usersObject = snapshot.val();
            const blogList = Object.keys(usersObject).map(key => ({
                ...usersObject[key]/*,
                uid: key,*/
            }));
            this.setState({
                blogs: blogList,
            });

            for (let i = 0; i < blogList.length; i++) {

                if (this.props.location.pathname.split("/")[2] === blogList[i].path) {
                    this.setState({
                        blog: blogList[i],
                    });
                    break
                }
            }


        });
    }

    render() {
        var ReactDOMServer = require('react-dom/server')
        var HtmlToReactParser = require('html-to-react').Parser
        var htmlToReactParser = new HtmlToReactParser()

        function handleClick() {
            // window.open("/contact", "_self")
            window.open("mailto:sherpa.ngima@hotmail.com", "_blank")
        }

        return (
            <div className="content">
                <Row>
                    <Col md="12">

                        <Card className="">
                            <CardBody>
                                <div className="blog">
                                    <a onClick={e => e.preventDefault()}>
                                        <h5 className="title">{this.state.blog.title}</h5>
                                        <img
                                            alt="..."
                                            className="blog-cover"
                                            src={this.state.blog.cover}
                                        />
                                    </a>
                                    <div className="body">
                                        {
                                            htmlToReactParser.parse(this.state.blog.body)
                                        }
                                    </div>
                                </div>
                            </CardBody>
                            <CardFooter>
                                <div className="button-container">
                                    <Button className="btn-icon btn-round" color="facebook">
                                        <a href="https://github.com/ngima" target="_blank"> <i
                                            className="fab fa-github"/></a>
                                    </Button>
                                    <Button className="btn-icon btn-round" color="facebook">
                                        <a href="mailto:sherpa.ngima@hotmail.com" target="_blank"> <i
                                            className="tim-icons icon-email-85"/></a>
                                    </Button>
                                    {/*<Button className="btn-icon btn-round" color="google">*/}
                                    {/*    <a href="https://dribbble.com/ngima" target="_blank"> <i*/}
                                    {/*        className="fab fa-dribbble"/></a>*/}
                                    {/*</Button>*/}

                                    <Button className="btn-icon btn-round" color="twitter">
                                        <a href="https://twitter.com/ngimaofficial" target="_blank"> <i
                                            className="fab fa-twitter"/></a>
                                    </Button>
                                </div>
                                {/*<div className="button-container">*/}

                                {/*    <Button className="btn-fill" color="primary" onClick={handleClick}>*/}
                                {/*        Hire me*/}
                                {/*    </Button>*/}
                                {/*</div>*/}
                            </CardFooter>
                        </Card>
                    </Col>
                </Row>
            </div>
        );
    }
}


const BlogDetails = withFirebase(BlogDetailsComponent)

export default BlogDetailsComponent;
export {BlogDetails};
