import React from "react";

// reactstrap components
import {
    Card,
    CardHeader,
    CardBody,
    CardTitle,
    // Table,
    Row,
    Col, /*CardText, CardFooter, Button,*/ CardLink
} from "reactstrap";
import {Line} from "react-chartjs-2";
import {withFirebase} from '../components/Firebase';
import AboutCompnent from "./About";


class LabsCompnent extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            work: [],
            loading: false
        };
    }

    componentDidMount() {
        //array
        this.props.firebase.labs().on('value', snapshot => {
            const workObject = snapshot.val();
            const workList = Object.keys(workObject).map(key => ({
                ...workObject[key]/*,
                uid: key,*/
            }));

            this.setState({
                work: workList
            })
        });
    }

    render() {

        function handleClick() {
            window.open("/contact", "_self")
        }

        return (
            <div className="content">
                <Row>
                    {this.state.work.map((item, key)=>
                        <Col lg="4" key={key}>
                            <Card className="card-chart">
                                <CardHeader>
                                    <CardTitle tag="h4">
                                        {item.name}
                                    </CardTitle>
                                </CardHeader>
                                <img src={item.image}/>

                                {item.link ?
                                    <CardBody className="center">
                                        <CardLink href={item.link} target="_blank">VIEW</CardLink>
                                    </CardBody>
                                    :""
                                }
                            </Card>
                        </Col>)
                    }
                </Row>
            </div>
        );
    }
}

const Labs = withFirebase(LabsCompnent)

export default LabsCompnent;
export {Labs};