import React from "react";

// reactstrap components
import {
    Card,
    CardHeader,
    CardBody,
    CardTitle,
    // Table,
    Row,
    Col, /*CardText, CardFooter, Button,*/ CardLink, CardSubtitle
} from "reactstrap";
import {Line} from "react-chartjs-2";
import {withFirebase} from '../components/Firebase';
import AboutCompnent from "./About";


class BlogCompnent extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            blogList: [],
            loading: false
        };
    }

    componentDidMount() {
        //array
        this.props.firebase.blogs().on('value', snapshot => {
            const workObject = snapshot.val();
            const blogList = Object.keys(workObject).map(key => ({
                ...workObject[key]/*,
                uid: key,*/
            }));

            this.setState({
                blogList: blogList
            })
        });
    }

    render() {

        function handleClick() {
            window.open("/contact", "_self")
        }

        return (
            <div className="content">
                <Row>
                    {this.state.blogList.map((item, key) =>
                        <Col lg="4" key={key}>
                            <Card className="card-chart">
                                <CardHeader>
                                    <CardTitle tag="h4">
                                        {item.title}
                                    </CardTitle>
                                    <CardSubtitle tag="h5">
                                        {item.categories}
                                    </CardSubtitle>
                                </CardHeader>
                                <img src={item.cover}/>

                                <CardBody className="center">
                                    <CardLink href={"/blog/" + item.path} target="_self">VIEW</CardLink>
                                </CardBody>
                            </Card>
                        </Col>)
                    }
                </Row>
            </div>
        );
    }
}

const Blogs = withFirebase(BlogCompnent)

export default BlogCompnent;
export {Blogs};