import React from "react";

// reactstrap components
import {
    Card,
    CardHeader,
    CardBody,
    CardTitle,
    Table,
    Row,
    Col, CardText, CardFooter, Button
} from "reactstrap";

import FirebaseComponent, {FirebaseComp} from "../components/Firebase/FirebaseComponent"
import {withFirebase} from '../components/Firebase';


class AboutCompnent extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            about: {
                name: "Ngima Sherpa",
                description: "loading...",
                designation: "loading...",
            },
            work: [],
            experience: [],
            skills: [],
            clients: [],
            loading: false
        };
    }

    componentDidMount() {
        //array
        this.props.firebase.experience().on('value', snapshot => {
            const usersObject = snapshot.val();
            const usersList = Object.keys(usersObject).map(key => ({
                ...usersObject[key]
                /*,
                uid: key,*/
            }));

            this.setState({
                experience: usersList
            })
        });
        //array
        this.props.firebase.skills().on('value', snapshot => {
            const usersObject = snapshot.val();
            const usersList = Object.keys(usersObject).map(key => ({
                ...usersObject[key]/*,
                uid: key,*/
            }));
            this.setState({
                skills: usersList,
            })

        });
        //array
        this.props.firebase.clients().on('value', snapshot => {
            const usersObject = snapshot.val();
            const usersList = Object.keys(usersObject).map(key => ({
                ...usersObject[key]/*,
                uid: key,*/
            }));
            this.setState({
                clients: usersList,
            })

        });
        //object
        this.props.firebase.about().on('value', snapshot => {
            const about = snapshot.val();
            this.setState({
                about: about
            })

        });
    }

    render() {

        function handleClick() {
            // window.open("/contact", "_self")
            window.open("mailto:sherpa.ngima@hotmail.com", "_blank")
        }

        return (
            <div className="content">
                <Row>
                    <Col md="12">

                        <Card className="card-user">
                            <CardBody>
                                <CardText/>
                                <div className="author">
                                    <div className="block block-one"/>
                                    <div className="block block-two"/>
                                    <div className="block block-three"/>
                                    <div className="block block-four"/>
                                    <a href="#" onClick={e => e.preventDefault()}>
                                        <img
                                            alt="..."
                                            className="avatar"
                                            src={require("assets/img/ngima_sherpa.jpg")}
                                        />
                                        <h5 className="title">{this.state.about.name}</h5>
                                    </a>
                                    <p className="description">{this.state.about.designation}</p>
                                </div>
                                <div className="card-description">{this.state.about.description}
                                </div>
                            </CardBody>
                            <CardFooter>
                                <div className="button-container">
                                    <Button className="btn-icon btn-round" color="facebook">
                                        <a href="https://github.com/ngima" target="_blank"> <i
                                            className="fab fa-github"/></a>
                                    </Button>
                                    <Button className="btn-icon btn-round" color="facebook">
                                        <a href="mailto:sherpa.ngima@hotmail.com" target="_blank"> <i
                                            className="tim-icons icon-email-85"/></a>
                                    </Button>
                                    {/*<Button className="btn-icon btn-round" color="google">*/}
                                    {/*    <a href="https://dribbble.com/ngima" target="_blank"> <i*/}
                                    {/*        className="fab fa-dribbble"/></a>*/}
                                    {/*</Button>*/}

                                    <Button className="btn-icon btn-round" color="twitter">
                                        <a href="https://twitter.com/ngimaofficial" target="_blank"> <i
                                            className="fab fa-twitter"/></a>
                                    </Button>
                                </div>
                                {/*<div className="button-container">*/}

                                {/*    <Button className="btn-fill" color="primary" onClick={handleClick}>*/}
                                {/*        Hire me*/}
                                {/*    </Button>*/}
                                {/*</div>*/}
                            </CardFooter>
                        </Card>
                    </Col>


                    <Col md="12">
                        <Card>
                            <CardHeader>
                                <h5 className="title">Clients</h5>
                                <p className="category">
                                    {/*worked for till*/}
                                    {/*<a href="https://nucleoapp.com/?ref=1712">NucleoApp</a>*/}
                                </p>
                            </CardHeader>
                            <CardBody className="all-icons">
                                <Row>

                                    {this.state.clients.map((item, key) =>

                                        <Col
                                            lg="2"
                                            md="3"
                                            sm="4"
                                        >
                                            <div className="client">
                                                <img className="client-logo"
                                                     src={item.image}/>
                                                {/*<p><a href={item.link} target="_blank">*/}
                                                    <section className="name">
                                                    <a>
                                                        <span className="text">{item.name}</span>
                                                        <span className="line -right"></span>
                                                        <span className="line -top"></span>
                                                        <span className="line -left"></span>
                                                        <span className="line -bottom"></span>
                                                    </a>
                                                </section>
                                                {/*</a></p>*/}
                                            </div>
                                        </Col>)
                                    }

                                </Row>
                            </CardBody>
                        </Card>
                    </Col>

                    <Col md="5">
                        <Card>
                            <CardHeader>
                                <CardTitle tag="h3">Skills</CardTitle>
                            </CardHeader>
                            <CardBody>
                                <Table className="tablesorter" responsive>
                                    <thead className="text-primary">
                                    {this.state.skills.map((item, key) =>
                                        <tr key={key}>
                                            <th>{item.name}</th>
                                            <th>
                                                <progress value={item.progress} max="100"/>
                                            </th>
                                        </tr>)
                                    }
                                    </thead>
                                    <tbody>
                                    </tbody>
                                </Table>
                            </CardBody>
                        </Card>
                    </Col>
                    <Col md="7">
                        <Card>
                            <CardHeader>
                                <CardTitle tag="h3">Work Experience</CardTitle>
                            </CardHeader>
                            <CardBody>
                                <Table className="tablesorter" responsive>
                                    <thead className="text-primary">
                                    {this.state.experience.map((item, key) =>
                                        <tr key={key}>
                                            <th>{item.company}</th>
                                            <th>{item.position}</th>
                                            <th>
                                                {item.from + " - " + item.to}
                                            </th>
                                        </tr>)
                                    }
                                    </thead>
                                    <tbody>
                                    </tbody>
                                </Table>
                            </CardBody>
                        </Card>
                    </Col>
                    <Col md="12">
                        <Card>
                            <CardHeader>
                                <CardTitle tag="h3">Other Experiences</CardTitle>
                            </CardHeader>
                            <CardBody>
                                <Table className="tablesorter" responsive>
                                    <thead className="text-primary">
                                    {this.state.experience.map((item, key) =>
                                        <tr key={key}>
                                            <th>{item.company}</th>
                                            <th>{item.position}</th>
                                            <th>
                                                {item.from + " - " + item.to}
                                            </th>
                                        </tr>)
                                    }
                                    </thead>
                                    <tbody>
                                    </tbody>
                                </Table>
                            </CardBody>
                        </Card>
                    </Col>

                </Row>
            </div>
        );
    }
}


const About = withFirebase(AboutCompnent)

export default AboutCompnent;
export {About};
