import React from "react";

// reactstrap components
import {
    Card,
    CardHeader,
    CardBody,
    CardTitle,
    Table,
    Row,
    Col, CardText, CardFooter, Button, CardLink
} from "reactstrap";

import FirebaseComponent, {FirebaseComp} from "../components/Firebase/FirebaseComponent"
import {withFirebase} from '../components/Firebase';


class NotFoundCompnent extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            images: [],
        };
    }

    componentDidMount() {
        //array
        this.props.firebase.notfoundImages().on('value', snapshot => {
            const usersObject = snapshot.val();
            const usersList = Object.keys(usersObject).map(key => ({
                ...usersObject[key]/*,
                uid: key,*/
            }));
            this.setState({
                images: usersList,
            })

            console.log(usersList)

        });
    }

    render() {

        function handleClick() {
            window.open("/contact", "_self")
        }

        function shuffle(a) {
            for (let i = a.length - 1; i > 0; i--) {
                const j = Math.floor(Math.random() * (i + 1));
                [a[i], a[j]] = [a[j], a[i]];
            }
            return a;
        }

        return (
            <div className="content">
                <Row className="align-self-center">
                    <h1>
                                404 {this.props.location.pathname.split("/")[2]}
                    </h1>


                </Row>
            </div>
        );
    }
}


const NotFound = withFirebase(NotFoundCompnent)

export default NotFoundCompnent;
export {NotFound};
